.menu-container {
  font-family: sans-serif;
  background-color: white;
  min-height: 100vh;
}

.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: green;
  color: white;
  padding: 1rem;
}

.logo {
  font-size: 1.2rem;
  font-weight: bold;
}

.hamburger {
  font-size: 1.5rem;
  cursor: pointer;
}

.side-menu {
  position: absolute;
  top: 60px;
  right: 10px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
  padding: 10px;
  z-index: 10;
}

.side-menu button {
  display: block;
  width: 100%;
  background: none;
  border: none;
  padding: 8px;
  text-align: left;
  cursor: pointer;
}

.main-menu {
  padding: 2rem;
  text-align: center;
}

.main-button {
  display: block;
  width: 80%;
  max-width: 300px;
  margin: 1rem auto;
  padding: 1rem;
  background-color: green;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  cursor: pointer;
}

.admin-section {
  margin-top: 3rem;
  text-align: center;
}

.admin-button {
  display: block;
  width: 80%;
  max-width: 300px;
  margin: 0.5rem auto;
  padding: 1rem;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  cursor: pointer;
}
